<template>
  <div class="playmv">
    <video
      :preload="preload"
      :poster="videoImg"
      :height="height"
      :width="width"
      align="center"
      :controls="controls"
      :autoplay="autoplay"
    >
      <source :src="videoSrc" type="video/mp4" />
    </video>
    <div class="d-flex text-xs" style="justify-content: space-around">
        <span>{{mv.artistName}}</span>
        <span class="text-weight-7">{{mv.name}}</span>
        <span>{{mv.playCount}}</span>
        <span>{{mv.publishTime}}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "Video",
  data() {
    return {
      mv: [],
      videoSrc: "",
      videoImg: "",
      playStatus: "",
      muteStatus: "",
      isMute: true,
      isPlay: false,
      width: "100%", // 
      height: "100%", // 
      preload: "auto", //  <video>
      controls: true, // autoplayPlayer API
      autoplay: ""
    };
  },

  // ,muted:
  // autoplay: 'muted',
  methods: {
    async getMV() {
      const res = await this.$http.get("/mv/detail?mvid=" + this.$route.params.mv);
      this.mv = res.data.data;
      console.log(this.mv);
      this.videoSrc=this.mv.brs['240'];
      this.videoImg=this.mv.cover
    }
  },
  created(){
      this.getMV()
  }
};
</script>

